  box-sizing: border-box;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
}

::-webkit-scrollbar {
  width: 6px;
  background-color: #ffffff;
}

::-webkit-scrollbar-thumb {
  background-color: #d65a9c;
}

body {
  background-color: #ffffff;
  font-family: "Roboto Mono", monospace;
  position: relative;
  height: 100vh;
  overflow: hidden;
  margin: 0;
  overflow-y: auto;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 10px;
}
.nav-container p {
  font-weight: bold;
}

.nav-unordered-list {
  list-style: none;
  text-align: right;
}
.nav-unordered-list li {
  display: inline;
  padding: 10px;
}

.link {
  text-decoration: none;
  color: black;
}

.link-active {
  color: #d65a9c;
}

.profile-image-container {
  display: block;
  border-radius: 50%;
  overflow: hidden;
  background-color: white;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}
.profile-image-container img {
  width: 100%;
  height: auto;
  display: block;
}

.main-header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 50px;
  gap: 10px;
}

.about-header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 50px;
  gap: 10px;
  height: 500px;
}

.projects-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px;
  gap: 10px;
}

.main-header-div {
  display: flex;
  flex-direction: row;
  gap: 30px;
  align-items: center;
  width: 100%;
}

.header-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
}
.header-content button {
  border-radius: 5px;
  color: #ffffff;
  background-color: #d65a9c;
  padding: 10px 20px;
  border: 1px solid #d65a9c;
  cursor: pointer;
}
.header-content button:hover {
  color: #d65a9c;
  background-color: #ffffff;
  border: 0.1px solid #d65a9c;
}

.tech-stack {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 10px;
  padding: 100px 50px;
  background-color: #fadeed;
}

.tech-stack-images {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.tech-stack-images img {
  width: 150px;
  height: 150px;
}

.about {
  display: flex;
  flex-direction: column;
  background-color: #cad5e2;
  padding: 100px 50px;
}

.projects {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  padding: 100px 50px;
  align-items: flex-start;
}
.projects button {
  border-radius: 5px;
  color: #ffffff;
  background-color: #d65a9c;
  padding: 10px 20px;
  border: 1px solid #d65a9c;
  cursor: pointer;
}
.projects button:hover {
  color: #d65a9c;
  background-color: #ffffff;
  border: 0.1px solid #d65a9c;
}

.footer {
  padding: 50px;
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  height: 300px;
  color: white;
  background-color: #6a3ad3;
  gap: 10px;
}
.footer span {
  margin-top: 50px;
  font-size: 10px;
}

.footer-logos {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}
.footer-logos img {
  width: 50px;
  height: 50px;
}

.projects-div {
  margin-bottom: 50px;
}

.projects-card-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.project-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 10px;
  width: 300px;
  margin: 32px;
  padding: 16px;
  height: 300px;
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
.project-card button {
  border-radius: 5px;
  color: #ffffff;
  background-color: #d65a9c;
  padding: 10px 20px;
  border: 1px solid #d65a9c;
  cursor: pointer;
}
.project-card button:hover {
  color: #d65a9c;
  background-color: #ffffff;
  border: 0.1px solid #d65a9c;
